import React, { useEffect, useState } from "react";
import Header from "../header/Header";
import SideBar from "../sidebar/SideBar";
import Items from "./Layout/registerItems/Items";
import Button from "./UI/Button";
import { getCookieToken } from "../../store/common/Cookie";

const Main1 = (props) => {
    const [menu, setMenu] = useState({
      mainMenu: null,
      sideMenu: null,
    });
  
    useEffect(() => {
      const fetchData = async () => {
        try {
          const response = await fetch("https://www.insung.shop/jat/menus", {
            method: "GET",
            headers: {
              "X-ACCESS-TOKEN": getCookieToken(),
            },
          });
          const data = await response.json();
          if (!data.isSuccess) {
            console.log(data.message);
            return;
          }
          setMenu({
            mainMenu: data.result.mainMenuList,
            sideMenu: data.result.sideMenuList,
          });
        } catch (err) {
          console.log(err);
        }
      };
  
      fetchData();
    }, []);
  
    return (
      <React.Fragment>
        <div>
          <Header />
          <SideBar />
          {menu.mainMenu && (
            <Items
              title=" "
              subTitle="        "
              menu={menu.mainMenu}
            />
          )}
          {menu.sideMenu && (
            <Items
              title="  "
              subTitle="        "
              menu={menu.sideMenu}
            />
          )}
          <Button />
        </div>
      </React.Fragment>
    );
  };
  

export default Main1;
