import classes from './OrderStatus.module.css'
import OrderButton from '../../UI/OrderButton'
import { useState } from 'react';

const OrderStatus = (props) => {

    const [activeButton, setActiveButton] = useState('waiting');

    const onReceptionHandler = (reception) => {
        props.onReceptionHandler(reception)
    }

    const onActiveHandler = (reception) => {
        setActiveButton(reception);
    };

    return (
        <div className={classes['order-container']}>
            <OrderButton classes='order' active={activeButton === 'waiting' ? 'active' : ''} content='' onReceptionHandler={onReceptionHandler} reception='waiting' onActiveHandler={onActiveHandler} />
            <OrderButton classes='order' active={activeButton === 'process' ? 'active' : ''} content='' onReceptionHandler={onReceptionHandler} reception='process' onActiveHandler={onActiveHandler} />
            <OrderButton classes='order' active={activeButton === 'complete' ? 'active' : ''} content='' onReceptionHandler={onReceptionHandler} reception='complete' onActiveHandler={onActiveHandler} />
            <OrderButton classes='order' content='  / ' to='/today/menu' />
            <OrderButton classes='finish' content=' ' onShowStore={props.onShowStore} />
        </div>
    )
}

export default OrderStatus;