import React from "react";
import Footer from "../footer/Footer";
import Header from "../header/Header";
import style from "./IDShow.module.css";
import { useLocation, useNavigate } from "react-router-dom";

export default function IDShow() {

  const location = useLocation();
  const navigate = useNavigate();

  const moveLoginHandler = () => {
    navigate('/login')
  }

  const moveForgotPasswordHandler = () => {
    navigate('/help')
  }

  return (
    <div className={style.container}>
      <Header />
      <div className={style.IDShow}>
        <span className={style.title}> -  </span>
        <div className={style.result}>
          <span className={style.result_title}>
               
          </span>
          <p className={style.result_desc}>
                .    
             .
          </p>
          <span className={style.result_id}>{location.state.uid}</span>
          <p className={style.result_date}>{location.state.signUpDate} </p>
        </div>
        <button className={style.bluebutton} onClick={moveLoginHandler}> </button>
        <p className={style.passwordlost} onClick={moveForgotPasswordHandler}> ?</p>
      </div>
      <Footer />
    </div>
  );
}
