import React, { useState, useEffect } from "react";
import style from "./IDSearchPhone.module.css";

export default function IDSearchPhone({ remainHandler, resultPhoneNumHandler }) {
  const [remainingTime, setRemainingTime] = useState(180); //    3(180) 
  const [certificationNum, setCertificationNum] = useState("");

  const handlePhoneNumberChange = (e) => {
    const inputPhoneNumber = e.target.value;
    setCertificationNum(inputPhoneNumber);
    resultPhoneNumHandler(inputPhoneNumber);
  };

  useEffect(() => {
    if (remainingTime > 0) {
      const timer = setTimeout(() => {
        setRemainingTime((prevTime) => prevTime - 1); // 1 
      }, 1000);

      return () => clearTimeout(timer); //     
    } else {
      alert("  .   .");
      remainHandler();
    }
  }, [remainingTime, remainHandler]);

  return (
    <>
      <form action="" className={style.ID_SEARCH}>
        <label htmlFor=""></label>
        <div className={style.inputContainer}>
          <input
            type="text"
            placeholder=" "
            className={style.certification}
            value={certificationNum}
            onChange={handlePhoneNumberChange}
          />
          <span className={style.remainingTime}>
            {Math.floor(remainingTime / 60)} {remainingTime % 60}
          </span>
        </div>
      </form>
      <p className={style.phoneretry}>
             1   .
           <strong> </strong>
        .
      </p>
    </>
  );
}
